/* Navbar.css */
.header {
    position: sticky;
    top: 0;
    min-height: 80px;
    width: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    overflow: hidden;
    background-image: linear-gradient(to left, #3ab5b0 0%, #3d99be 31%, #042e81 100%);
}

.logo {
    text-decoration: none;
    text-transform: uppercase;
    color: white;
    padding-left: 40px;
}

.header-links {
    display: flex;
    align-items: center;
}

.header-links ul {
    display: flex;
    align-items: center;
    margin: 0;
    padding: 0;
}



.header-links ul li {
    list-style: none;
    display: inline-block;
    font-size: 18px;
    /* margin-right: 20px; */
}

.header-links ul li a {
    text-decoration: none;
    text-transform: uppercase;
    color: white;
    padding: 0px 15px;
    cursor: pointer;
}

.header-links ul li a:hover {
    color: aqua;
}

.cart {
    display: flex;
    align-items: center;
    position: relative;
    margin-right: 40px;
}

.cart i {
    font-size: 24px;
    color: white;
}

.cart-length {
    background-color: #ff4d4d;
    color: white;
    border-radius: 50%;
    padding: 4px 8px;
    font-size: 14px;
    font-weight: bold;
    position: absolute;
    top: -10px;
    right: -10px;
    display: flex;
    justify-content: center;
    align-items: center;
}
