.products {
    margin: 20px auto;
    width: 80%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    border-radius: 5px;
}

.card {
    width: calc(50% - 20px); /* Adjusted width for desktop view */
    max-width: 260px; /* Decreased max-width of the card */
    height: auto; /* Adjusted height for responsive image */
    border: 1px solid gray;
    border-radius: 5px;
    overflow: hidden;
    padding: 10px;
    margin-bottom: 20px;
    background-color: white;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
}

.product-image {
    width: 100%;
    height: 200px; /* Adjusted height for better image consistency */
    display: block;
    object-fit: contain;
}

.product-details {
    padding: 10px 0;
}

.product-name {
    text-transform: uppercase;
    margin-bottom: 5px;
    font-weight: bold;
    text-align: center; /* Centered product name */
}

.product-price {
    font-size: 16px;
    font-weight: bold;
    color: #007bff;
    text-align: center; /* Centered product price */
}

.add-button {
    font-size: 14px;
    /* font-weight: bold; */
    border-radius: 25px;
    border: none;
    outline: none;
    background-color: yellowgreen;
    color: white;
    padding: 9px 30px;
    cursor: pointer;
    text-transform: uppercase;
    margin-top: 20px;
    display: block;
    margin: 0 auto;
}


    .banner-image {
        width: 100%;
        height: 500px;
    }


/* Media Query for responsiveness */
@media screen and (max-width: 768px) {
    .card {
        width: calc(50% - 20px);
    }
}

@media screen and (max-width: 480px) {
    .card {
        width: calc(100% - 20px); /* Adjusted width for smaller screens */
    }
    .banner-image {
        width: 100%;
        height: auto;
    }
}


.added-to-cart-message {
    background-color: #dff0d8;
    color: #3c763d;
    padding: 10px;
    border: 1px solid #d6e9c6;
    border-radius: 5px;
    margin-top: 10px;
    width: 100%; 
    text-align: center;
}

.container-fluid footer ul {
    display: flex;
    list-style-type: none;
    padding: 20px;
    margin: 0;
    justify-content: center; 
    background-image: linear-gradient(to left, #3ab5b0 0%, #3d99be 31%, #042e81 100%);
}

.container-fluid footer ul li {
        margin-right: 20px;
  }
  
  .container-fluid footer ul li:last-child {
    margin-right: 0;
  }
  
  .container-fluid footer ul li a {
    text-decoration: none;
    color: #ffffff;
  }
  
  .container-fluid footer ul li a:hover {
    text-decoration: underline;
  }


  .Heading{
    text-align: center;
    margin-top: 2rem;
    margin-bottom: 2.3rem;
    background-image: linear-gradient(to left, #3ab5b0 0%, #3d99be 31%, #042e81 100%);
    -webkit-background-clip: text;
    color: transparent;
  }
  